import React,{Component} from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom'; 
import DefaultImage from '../resources/NoNoteImage.png';
import urlConst from '../config/config';
import Background from '../resources/dashboard_image.jpg';

export default class Notes extends Component{
    
state: {notes: any[]} = {
    notes: []
}

    componentDidMount(){
        let id=localStorage.getItem('user_id_forNotes');
        if(id!=='')
        {
            axios.get(urlConst.url +`/notelist/${id}`)
           .then(resp =>{
            this.setState({
                notes:resp.data
            });
            });
        }
        else{
            axios.get(urlConst.url +'/allnotes',
            // { headers: {'authorization': `${localStorage.token}`}}
            )
            .then(resp =>{
                this.setState({
                    notes:resp.data.data
                });
            });
        }        
    }
      
    render(){
        return(
            <div style={{ backgroundColor: "black", backgroundImage: `url(${Background})`, height: "657px", backgroundSize: 'cover', backgroundAttachment:'fixed' }}>
            <div className='container' style={{width:'900px'}}>
                <br/><br/>
                <div>    
                <h3 style={{ color: "#ffe20f",position: 'fixed', left: '50px' }}>All Notes List</h3>
                {localStorage.getItem('user_id_forNotes') !==''?<Link to="/users" className='btn btn-outline-primary' style={{position: 'fixed',right: '285px'}}>Back</Link>: null}
                <Link to="/dashboard" className='btn btn-outline-primary' style={{position: 'fixed',right: '130px'}}>Go to Dashboard</Link>
                <Link to="/" className='btn btn-danger' style={{position: 'fixed',right: '34px'}}>Sign Out</Link>                
                </div>
                <br/><br/>
                {localStorage.getItem('user_id_forNotes') !=='' && this.state.notes.length === 0?<p>No Data to display</p>: null}
                <br/>
                <div className='row' style={{overflow:'scroll', height:480, overflowX:'hidden'}}>
                    {this.state.notes.map(note => {
                        return(
                            <div className='col col-12' key={note._id}>
                                <div className='card'>
                                     <div className='card-header' style={{backgroundColor:'#ccccff'}} >
                                        <div className='row'>
                                            <div className="col-10">
                                                <h3>{note.title}</h3>
                                            </div>
                                            <div className="col-2">

                                            </div>
                                        </div>
                                    </div>                                    
                                    <div className="row card-body ">
                                        <div className="col-6">
                                            <p><b>Body : </b>{note.body}</p>
                                            <p><b>Note Color : </b>{note.noteColor}</p>                                             
                                            <div className='border border-dark' style={{backgroundColor:note.noteColor, height:70,width:70,borderRadius:50}}></div>                                            
                                            {localStorage.getItem('user_id_forNotes')!==''? null:<p><b>Created By : </b>{note.creator}</p>}
                                            
                                        </div>
                                        <div className="col-6">
                                            {/* <p><b>Image :</b> {note.image}</p> */}
                                            {note.image === ''?
                                            <img src={DefaultImage}  style={{height:150, width:150, float:'right'}} />
                                            :<img src={note.image} style={{height:150, width:150, float:'right'}} />}    
                                        </div>
                                    </div>                                    
                                </div>                                
                                <br/> 
                            </div>
                        )
                    })}
                </div>
            </div>
            </div>
        )
    }
}